import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../context/authContext';
import homeImg from '../img/home-icon.png'
import searchImg from '../img/search-icon.png'
import websiteImg from '../img/website-logo.png'
import Box from '@mui/material/Box';
import LinearProgress from '@mui/material/LinearProgress';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { IconButton } from '@mui/material';
import { api } from '../api';

function NavBar() {
  const { setIsLoggedIn, isLoggedIn } = useAuth();
  const [loading, setLoading] = useState(false);
  const [user, setUser] = useState(null);
  const [nav, setNav] = useState(false);

  const history = useNavigate();

  useEffect(() => {
    statusCheck(); // Check the login status when the component mounts
  }, []);

  const statusCheck = () => {
    fetch(`https://blog-website-api-91b4.onrender.com/api/v1/user/verify`)
      .then((response) => response.json())
      .then((data) => {
        setIsLoggedIn(data.success);
        setUser(data.user);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const logout = () => {
    fetch(`https://blog-website-api-91b4.onrender.com/api/v1/user/signout`)
      .then((response) => response.json())
      .then(() => {
        toast('Logout Successful !')
        setIsLoggedIn(false);
        history('/');
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const navHandal = () => {
    console.log("click")
    setNav(nav ? false : true);
  }

  return (
    <>
      <div className="nav-bar">
        <ToastContainer></ToastContainer>
        <header>
          <div className="title">
            <img className="icon" src={websiteImg} alt="" />
            Blog Website
          </div>
          <div className="main-menu">
            <ul>
              <li><Link style={{ textDecoration: "none" }} to="/">HOME</Link></li>
              {isLoggedIn ? (
                <>
                  <li>{user ? <>{user.name}</> : null}</li>
                  <li> <Link style={{ textDecoration: "none" }} to="/myblogs">MY BLOGS</Link></li>
                  <li className='btn' onClick={logout}>LOGOUT</li>
                </>
              ) : (
                <>
                  <h1></h1>
                  <li><Link style={{ textDecoration: "none" }} className='btn' to="/signin">SIGNIN</Link></li>
                  <li><Link style={{ textDecoration: "none" }} className='btn' to="/signup">SIGNUP</Link></li>
                </>
              )}
            </ul>
          </div>

          <div className="sm-menu-btn">
            <span className="material-symbols-outlined" onClick={navHandal}>
              lists
            </span>
            {nav ? (
              <div className="sm-menu" id="sm-menu-id">
                <ul className="sm-login-group">


                  {isLoggedIn ? (
                    <>
                      <li>{user ? <>{user.name}</> : null}</li>
                      <li> <Link style={{ textDecoration: "none", color: "white" }} to="/myblogs"><span class="material-symbols-outlined">
                        rss_feed
                      </span>MY BLOGS</Link></li>
                      <li className='btn' style={{ color: "white" }} onClick={logout}> <span className="material-symbols-outlined">Logout</span>LOGOUT</li>
                    </>
                  ) : (
                    <>
                      <h1></h1>
                      <li><Link style={{ textDecoration: "none", color: "white" }} className='account' to="/signin"><span className="material-symbols-outlined">Login</span>SIGNIN</Link></li>
                      <li><Link style={{ textDecoration: "none", color: "white" }} className='card' to="/signup"><span className="material-symbols-outlined">person_add</span>SIGNUP</Link></li>
                    </>
                  )}
                  <div className="close"><span className="material-symbols-outlined" onClick={navHandal}> close</span></div>



                </ul>
                <ul className="sm-option">
                  <Link  style={{ textDecoration: "none", color:"white" }} to="/">HOME</Link>
                </ul>
              </div>
            ) : <></>}
          </div>
        </header>

        {loading && (
          <Box sx={{ width: '100%' }}>
            <LinearProgress sx={{ color: '#7e22ce' }} />
          </Box>
        )}
      </div>
    </>
  );
}

export default NavBar;
